import { Box, Grid,Typography, CardMedia } from "@mui/material";
import React from "react";

const Google = (props) => {

  console.log(props.data)
  
  
  return (
    <Box sx={{ marginTop: " 50px" }}>
      
      <Grid
        container
        sx={{
          position: "relative",
          display: "flex",
          justifyContent: "center",
          gap: "50px",
          width: "90vw",
          padding: "40px",
          borderRadius: "20px",
          backgroundColor: "white",
          margin: "auto",
        }}
      >
        <CardMedia
          component="img"
          image="https://static.wixstatic.com/media/b54bb7_2e4d4ebe4c4e471989ae879e31e0484c~mv2.png/v1/fill/w_54,h_281,al_c,q_85,usm_0.66_1.00_0.01,enc_auto/Group%201.png"
          sx={{
            position: "absolute",
            zIndex: "-100",
            bottom: "-150px",
            left: "130px",
            width: "50px",
            transform: "rotate(90deg)",
          }}
        />
        {props.data.map((item) => {
          return (
            <Grid item xs={10} md={3} sx={{ textAlign: "center" }}>
              <Grid
                container
                sx={{
                  display: "flex",
                  alignItems: "center",
                  marginBottom: "50px",
                }}
              >
                <Grid item>
                  <CardMedia
                    component="img"
                    sx={{ width: "70px", height: "70px", borderRadius: "100%" }}
                    title="user"
                    image={item.imgOne}
                  />
                </Grid>
                <Grid item sx={{ paddingLeft: "40px" }}>
                  <CardMedia
                    component="img"
                    sx={{ width: "100px" }}
                    title="google"
                    image={item.imgTwo}
                  />
                  <CardMedia
                    component="img"
                    sx={{ width: "100px", marginTop: "5px" }}
                    title="google"
                    image={item.imgThree}
                  />
                </Grid>
              </Grid>
              <Typography
                variant="p"
                sx={{ fontSize: "16px", marginTop: "35px", color: "#757575" }}
                color="inherit"
              >
                {item.p}
              </Typography>
              <Typography
                variant="h4"
                sx={{
                  fontSize: "18px",
                  textAlign: "center",
                  marginTop: "35px",
                  color :"black",
                  fontWeight :"bold"
                }}
                color="inherit"
              >
                {item.h4}
              </Typography>
            </Grid>
          );
        })}
      </Grid>
    </Box>
  );
};

export default Google;
