import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Box,
  Typography,
} from "@mui/material";
import React from "react";

import AccordionActions from "@mui/material/AccordionActions";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import Button from "@mui/material/Button";
const Accor = (props) => {
  
  return (
    <Box sx={{ width: "80vw", margin: "auto" }}>
      <Typography
        variant="h1"
        color="inherit"
        sx={{
          fontSize: "30px",
          marginBottom: "30px",
          marginTop: "160px",
          fontWeight: "bold",
        }}
      >
        Frequently Asked Question
      </Typography>
      {props.acc.map((item) => {
        return (
          <Accordion
            sx={{
              padding: "5px",
              borderRadius: "15px 15px 0px 0px",
              boxShadow:
                "1px 1px 2px rgb(225, 47 ,143) , -1px -1px 2px rgb(225, 47 ,143,0.5)",
            }}
          >
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1-content"
              sx={{
                fontSize: "20px",
                fontWeight: "bold",
                "&:hover": {
                  color: "rgb(225, 47 ,143)",
                },
              }}
            >
              {item.summary}
            </AccordionSummary>
            <AccordionDetails>{item.text}</AccordionDetails>
          </Accordion>
        );
      })}
      {/* <Accordion>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel2-content"
          id="panel2-header"
        >
          Accordion 2
        </AccordionSummary>
        <AccordionDetails>
          Lorem ipsum dolor sit amet, consectetur adipiscing elit. Suspendisse
          malesuada lacus ex, sit amet blandit leo lobortis eget.
        </AccordionDetails>
      </Accordion>
      <Accordion defaultExpanded>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel3-content"
          id="panel3-header"
        >
          Accordion Actions
        </AccordionSummary>
        <AccordionDetails>
          Lorem ipsum dolor sit amet, consectetur adipiscing elit. Suspendisse
          malesuada lacus ex, sit amet blandit leo lobortis eget.
        </AccordionDetails>
        <AccordionActions>
          <Button>Cancel</Button>
          <Button>Agree</Button>
        </AccordionActions>
      </Accordion> */}
    </Box>
  );
};

export default Accor;
